body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  transition: background 0.3s ease;
}

#root {
  min-height: 100vh;
}

body[data-theme='dark'] {
  background: linear-gradient(135deg, #1a1a1a 0%, #0a0a0a 100%);
  color: #ffffff;
}

body[data-theme='light'] {
  background: linear-gradient(135deg, #ffffff 0%, #f0f0f0 100%);
  color: #000000;
}

.playlist-container {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  height: calc(100vh - 200px);
  overflow-y: auto;
}

.playlist-item {
  margin: 10px 0;
  padding: 15px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  cursor: pointer;
}

.playlist-item:hover {
  transform: translateX(10px);
  background: rgba(255, 255, 255, 0.2);
}

.youtube-player-container {
  position: relative;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 20px;
  margin: 20px 0;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.player-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.now-playing {
  font-size: 1.2rem;
  font-weight: 500;
  background: linear-gradient(45deg, #ff6b6b, #feca57);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 700;
  letter-spacing: -1px;
  cursor: pointer;
  transition: opacity 0.3s ease;
}

.app-title {
  background: linear-gradient(45deg, #FF512F, #F09819, #FF8C42, #FFC75F, #FF512F);
  background-size: 400% auto;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 900;
  letter-spacing: -1.5px;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  animation: shine 8s linear infinite;
  text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.15);
  position: relative;
}

.app-title:hover {
  opacity: 1;
  transform: scale(1.05) translateY(-3px);
  text-shadow: 0 0 40px rgba(255, 140, 66, 0.6);
  letter-spacing: -0.5px;
}

.app-title::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, transparent, #FF8C42, transparent);
  transform: scaleX(0);
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.app-title:hover::after {
  transform: scaleX(1);
}

.youtube-player-container {
  position: relative;
  background: rgba(255, 255, 255, 0.08);
  border-radius: 24px;
  padding: 30px;
  margin: 25px 0;
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.2),
              0 5px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.youtube-player-container:hover {
  transform: translateY(-8px) scale(1.01);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3),
              0 8px 25px rgba(0, 0, 0, 0.15);
}

.playlist-container {
  background: rgba(255, 255, 255, 0.06);
  border-radius: 24px;
  padding: 30px;
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.12);
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
  height: calc(100vh - 200px);
  overflow-y: auto;
  transition: all 0.5s ease;
  scrollbar-width: thin;
  scrollbar-color: rgba(255, 255, 255, 0.3) transparent;
}

.playlist-item {
  margin: 15px 0;
  padding: 20px;
  border-radius: 18px;
  background: rgba(255, 255, 255, 0.06);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.08);
  position: relative;
  overflow: hidden;
}

.playlist-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.05), transparent);
  transform: translateX(-100%);
  transition: transform 0.6s ease;
}

.playlist-item:hover {
  transform: translateX(12px) scale(1.03);
  background: rgba(255, 255, 255, 0.12);
  border-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.playlist-item:hover::before {
  transform: translateX(100%);
}

@keyframes shine {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.video-info {
  padding: 15px;
  margin-top: 15px;
  background: rgba(255,255,255,0.05);
  border-radius: 12px;
  border: 1px solid rgba(255,255,255,0.1);
}

@keyframes gradient {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

